.loading-holder {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  background-color: rgba(36, 42, 51, 0.5);
  z-index: 100000;
}
.loading {
  position: absolute;
  left: calc(50% - 100px);
  /* left: 43em;*/
  margin: auto;
  z-index: 999;
  top: calc(40vh - 100px);
  width: 200px;
  height: 200px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1), 0 5px 10px 0 rgba(0, 0, 0, 0.1);
  padding-top: 30px;
  -webkit-filter: none !important;
          filter: none !important;
  border-radius: 50%;
}


.loader1,
.loader1:before,
.loader1:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load1 1.8s infinite ease-in-out;
  animation: load1 1.8s infinite ease-in-out;
}
.loader1 {
  color: #33b8ff;
  font-size: 10px;
  margin: 30px auto 50px auto;
  position: relative;
  text-indent: -9999em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader1:before,
.loader1:after {
  content: '';
  position: absolute;
  top: 0;
}
.loader1:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader1:after {
  left: 3.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
.loader2-holder {
  width: 4.5em;
  padding-left: 0;
  padding-right: 15px;
}
.loader2,
.loader2:before,
.loader2:after {
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load2 1.8s infinite ease-in-out;
  animation: load2 1.8s infinite ease-in-out;
}
.loader2 {
  color: #33b8ff;
  font-size: 10px;
  margin: 10px auto 0 auto;
  position: relative;
  text-indent: -9999em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader2:before,
.loader2:after {
  content: '';
  position: absolute;
  top: 0;
}
.loader2:before {
  left: -2em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader2:after {
  left: 2em;
}
@-webkit-keyframes load2 {
  0%,
  80%,
  100% {
    box-shadow: 0 1.5em 0 -0.7em;
  }
  40% {
    box-shadow: 0 1.5em 0 0;
  }
}
@keyframes load2 {
  0%,
  80%,
  100% {
    box-shadow: 0 1.5em 0 -0.7em;
  }
  40% {
    box-shadow: 0 1.5em 0 0;
  }
}
