	/*vertical flight area*/
	#v-flight {
		padding-left: 0;
		padding-right: 0;
		padding-top: 19px;
		padding-bottom: 19px;
	}
	#v-flight {
		text-align: left;
	}
	#v-flight h4 {
		font-weight: 700;
		font-size: 18px;
	}
	#v-flight h4.title_inbound {
		margin-top: 2em;
	}
	#v-flight > div {
		width: 90%;
		margin: 0 auto;
	}
	#v-flight .main-body {
		margin-left: 1.5em;
		position: relative;
	}
	#v-flight .main-body > div > div:nth-child(2) {
		margin-left: 1.7em;
	}
	#v-flight .main-body span.ball {
		display: inline-block;
		height: 14px;
		width: 14px;
		border: 1px solid #20252d;
		border: 1px solid var(--shadow);
		border-radius: 360px;
		margin-right: 0.5em;
		vertical-align: -2px;
		z-index: 1;
	}
	#v-flight .main-body span.ball.filled {
		background-color: #20252d;
		background-color: var(--shadow);
	}
	#v-flight .main-body .dashed-line {
		position: absolute;
		height: 2em;
		width: 1em;
		border-left: 2px dashed #20252d;
		border-left: 2px dashed var(--shadow);
		left: 6px;
		top: 17px;
	}
	#v-flight .flight-details-v {
		margin-top: 10px;
		color: #97a7bb;
		color: var(--svg);
		visibility: hidden;
		position: absolute;
		z-index: -1;
		-webkit-transform: scale(1, 0);
		transform: scale(1, 0);
		-webkit-transition: -webkit-transform 200ms 100ms;
		transition: -webkit-transform 200ms 100ms;
		transition: transform 200ms 100ms;
		transition: transform 200ms 100ms, -webkit-transform 200ms 100ms;
	}
	#v-flight .flight-details-v.show {
		visibility: visible;
		z-index: 0;
		position: relative;
		-webkit-transform: scale(1, 1);
		transform: scale(1, 1);
		font-size: 12px;
	}
	#v-flight .flight-sum-bar .displayFunction {
		position: absolute;
		top: 1px;
		right: 0;
	}
	#v-flight .city-name {
		max-width: 90%;
	}

	.flight-sum-bar span.box {
		display: inline-block;
		border: 1px solid #38c7a3;
		border: 1px solid var(--highlight);
		border-radius: 2px;
		height: 17px;
		width: 17px;
		vertical-align: baseline;
		margin-right: 0.4em;
		color: #38c7a3;
		color: var(--highlight);
		position: relative;
	}
	.flight-sum-bar span.box::after {
		content: "+";
		position: absolute;
		font-size: 1.4em;
		left: 2px;
		top: -6px;
	}
	.flight-sum-bar span.box.expand::after {
		content: "-";
		left: 4px;
		top: -8px;
	}
	.flight-sum-bar span.desc {
		vertical-align: 2px;
		color: #38c7a3;
		color: var(--highlight);
		font-size: 16px;
		font-family: Roboto, Montserrat !important;
	}
	.flight-sum-bar .displayFunction {
		z-index: 10;
