	.add-animation-shake {
		-webkit-animation: shake 1s;
		animation: shake 1s;
		-webkit-animation-iteration-count: infinite;
		animation-iteration-count: infinite;
	}
	@-webkit-keyframes shake {
		0% {
			-webkit-transform: translate(1px, 1px) rotate(0deg);
			transform: translate(1px, 1px) rotate(0deg);
		}
		10% {
			-webkit-transform: translate(-2px, -1px) rotate(1deg);
			transform: translate(-2px, -1px) rotate(1deg);
		}
		20% {
			-webkit-transform: translate(3px, 0px) rotate(0deg);
			transform: translate(3px, 0px) rotate(0deg);
		}
		30% {
			-webkit-transform: translate(-3px, 1px) rotate(-2deg);
			transform: translate(-3px, 1px) rotate(-2deg);
		}
		40% {
			-webkit-transform: translate(1px, -1px) rotate(-1deg);
			transform: translate(1px, -1px) rotate(-1deg);
		}
		50% {
			-webkit-transform: translate(0px, 0px) rotate(0deg);
			transform: translate(0px, 0px) rotate(0deg);
		}
		60% {
			-webkit-transform: translate(1px, 1px) rotate(0deg);
			transform: translate(1px, 1px) rotate(0deg);
		}
		70% {
			-webkit-transform: translate(-2px, -1px) rotate(1deg);
			transform: translate(-2px, -1px) rotate(1deg);
		}
		80% {
			-webkit-transform: translate(4px, 1px) rotate(0deg);
			transform: translate(4px, 1px) rotate(0deg);
		}
		90% {
			-webkit-transform: translate(-4px, 0px) rotate(-2deg);
			transform: translate(-4px, 0px) rotate(-2deg);
		}
		100% {
			-webkit-transform: translate(1px, -1px) rotate(-1deg);
			transform: translate(1px, -1px) rotate(-1deg);
		}
	}
	@keyframes shake {
		0% {
			-webkit-transform: translate(1px, 1px) rotate(0deg);
			transform: translate(1px, 1px) rotate(0deg);
		}
		10% {
			-webkit-transform: translate(-2px, -1px) rotate(1deg);
			transform: translate(-2px, -1px) rotate(1deg);
		}
		20% {
			-webkit-transform: translate(3px, 0px) rotate(0deg);
			transform: translate(3px, 0px) rotate(0deg);
		}
		30% {
			-webkit-transform: translate(-3px, 1px) rotate(-2deg);
			transform: translate(-3px, 1px) rotate(-2deg);
		}
		40% {
			-webkit-transform: translate(1px, -1px) rotate(-1deg);
			transform: translate(1px, -1px) rotate(-1deg);
		}
		50% {
			-webkit-transform: translate(0px, 0px) rotate(0deg);
			transform: translate(0px, 0px) rotate(0deg);
		}
		60% {
			-webkit-transform: translate(1px, 1px) rotate(0deg);
			transform: translate(1px, 1px) rotate(0deg);
		}
		70% {
			-webkit-transform: translate(-2px, -1px) rotate(1deg);
			transform: translate(-2px, -1px) rotate(1deg);
		}
		80% {
			-webkit-transform: translate(4px, 1px) rotate(0deg);
			transform: translate(4px, 1px) rotate(0deg);
		}
		90% {
			-webkit-transform: translate(-4px, 0px) rotate(-2deg);
			transform: translate(-4px, 0px) rotate(-2deg);
		}
		100% {
			-webkit-transform: translate(1px, -1px) rotate(-1deg);
			transform: translate(1px, -1px) rotate(-1deg);
		}
	}

